import React from "react";

export function UserDisplay(props) {
    const {user, includeFullName} = props
    const NameDisplay = includeFullName === true? `${user.FirstName} ${user.LastName}${' '}` : null

    return <React.Fragment>
        {NameDisplay}
        <Userlink username={user.username}>@{user.username}</Userlink>
    </React.Fragment>
}

export function Userlink(props) {
    const {username} = props
    const handlelink = (event) => {
        window.location.href = `/profile/${username}`
    }
    return <span className='pointer' onClick={handlelink}>
        {props.children}
    </span>
}


export function UserPicture(props) {
    const {user} = props
    return <Userlink username={user.username}><span className='px-3 py-2 rounded-circle bg-success text-white'>
               {user.username[0]}
    </span></Userlink>
}
